				"html": "<h2>Machine Learning</h2>\n<!-- The article goes here, in GitHub-flavored Markdown. Feel free to add YouTube videos, images, and CodePen/JSBin embeds  -->\n<p>As a pioneer in artificial intelligence Arthur Samuel defines it in 1959, Machine Learning is \"the field of study that gives computers the ability to learn without being explicitly programmed.\" </p>\n<p>A more formal definition of Machine Learning is provided by Prof Tom Mitchell of CMU: \"A computer program is said to learn from experience E with respect to some class of tasks T and performance measure P if its performance at tasks in T, as measured by P, improves with experience E.\" Consider the example of a Machine Learning algorithm that plays chess. In this example, <code>E</code> refers to the experience of playing chess, <code>T</code> is the task of playing chess, and <code>P</code> denotes the probability that the program will win the next game of chess.</p>\n<p>In general, machine learning problems can be classified into supervised learning, and unsupervised learning. In supervised learning, you already have the input and the correct output, and know that a relationship exists between the input and the output. When you know neither what the correct output is nor if a relationship exists, unsupervised learning will help you derive structure if there is one.</p>\n<h4>More Information:</h4>\n<!-- Please add any articles you think might be helpful to read before writing the article -->\n<ol>\n<li><a href=\"https://en.wikipedia.org/wiki/Machine_learning\">Machine Learning on Wikipedia</a></li>\n</ol>\n<p>In general, any machine learning problem can be assigned to one of two broad classifications:</p>\n<p>1.Supervised learning.\n2.Unsupervised learning.</p>",
